import { Button, Form, Input, DatePicker, Select, Divider, Tag } from "antd";
import {
  HomeOutlined,
  CheckCircleOutlined,
  CloseCircleOutlined,
  FormOutlined,
  DeleteOutlined
} from "@ant-design/icons";
import {
  deliveryIcon,
  filterIcon,
  writtenIcon,
  interviewIcon,
  offerIcon,
} from "./datamap";
import sortIcon from "../../assets/img/icon-sort.png";
import sortIconUp from "../../assets/img/icon-sort-up.png";
import sortIconDown from "../../assets/img/icon-sort-down.png";
import sortIconUpHover from "../../assets/img/icon-sort-up-hover.png";
import sortIconDownHover from "../../assets/img/icon-sort-down-hover.png";

import React from "react";
import EditRecord from "./components/editRecord";

import "./index.css";
const options = [
  { value: 0, label: "已投递" },
  { value: 1, label: "简历筛选" },
  { value: 2, label: "笔试/测评中" },
  { value: 3, label: "面试中" },
  { value: 4, label: "已收offer" },
];
// -1 不通过，0等待，1通过
const { RangePicker } = DatePicker;
class RecordList extends React.Component {
  constructor() {
    super();
    this.state = {
      // 搜索栏状态
      company: "",
      stateDate: null,
      endDate: null,
      selectedStatus: 0,
      // 筛选栏状态
      sortType: "date",
      sortOptions: [
        { name: "按投递日期", value: "date", isUp: 0 },
        { name: "按流程进度", value: "status", isUp: undefined },
        { name: "按年薪", value: "money", isUp: undefined },
      ],
      // 表格数据
      tableSource: [
        {
          id: 0,
          company: "美团",
          city: '成都',
          link: "https://s3plus.meituan.net/zhaopin-official-website-prod/imgs/favicon.ico",
          status: 2,
          office: "前端开发",
          date: "2022/10/09",
          written_date: "2022/10/22",
          written: 1,
          first_interview_done: 1,
          first_date: "2022/10/26",
          first_desc: "表现良好",
          second_interview_done: -1,
          second_date: "2022/10/31",
          second_desc: "",
          hr_interview_done: 1,
          hr_date: "2022/11/04",
          hr_desc: "谈薪"
        },
      ],
      isDiaglog: false,
      // record: null
    };
    this.record = null
    
  }
  listSearchForm = React.createRef();
  changeSortType(e, option) {
    const newOptions = this.state.sortOptions.map((item) => {
      if (item.value !== option.value) {
        return { ...item, isUp: undefined };
      } else {
        return { ...item, isUp: !item.isUp ? 1 : 0 };
      }
    });
    this.setState({
      ...this.state,
      sortType: option.value,
      sortOptions: newOptions,
    });
  }
  // 删除投递记录
  deleteRecord(e, id) {
    console.log("delete", id)
  }
  // 编辑投递记录
  editRecord(e, item){
    // console.log(item);
    this.record = item
    this.setState({
      ...this.state,
      // record: item,
      isDiaglog: true
    })
  }

  render() {
    const { sortType, sortOptions, tableSource, isDiaglog } = this.state;

    return (
      <div className="record-list-container">
        <header className="record-header">XXX的秋招记录</header>
        <Button
          className="record-logout"
          type="default"
          onClick={() => {
            window.location.href = "./";
          }}
        >
          退出
        </Button>
        <div className="record-list-wrapper">
          <div className="record-list-search">
            <Form ref={this.listSearchForm} name="listSearch" layout="inline">
              <Form.Item name="company" label="企业">
                <Input
                  placeholder="请输入企业名称"
                  prefix={<HomeOutlined />}
                  allowClear
                />
              </Form.Item>
              <Form.Item label="投递日期" name="date">
                <RangePicker></RangePicker>
              </Form.Item>
              <Form.Item label="当前状态" name="status">
                <Select
                  defaultValue={0}
                  options={options}
                  value={this.selectedStatus}
                  onChange={(value) => (this.selectedStatus = value)}
                  style={{ width: "128px" }}
                  popupClassName="search-status-selector"
                  getPopupContainer={(e) => e.parentNode}
                ></Select>
              </Form.Item>
            </Form>
          </div>
          <div className="record-list-sort">
            <div className="sort-inner">
              <div className="list-sort-options">
                <ul>
                  {sortOptions.map((item) => (
                    <li
                      className={sortType === item.value ? "selected" : ""}
                      onClick={(e) => this.changeSortType(e, item)}
                      key={item.value}
                    >
                      {item.name}{" "}
                      <span className={item.isUp ? "up" : "down"}>
                        {sortType === item.value ? (
                          item.isUp ? (
                            <img
                              src={sortIconUp}
                              className="img-sort-up"
                              alt="sort-up"
                            />
                          ) : (
                            <img
                              src={sortIconDown}
                              className="img-sort-down"
                              alt="sort-down"
                            />
                          )
                        ) : (
                          <img src={sortIcon} alt="sort-icon" />
                        )}
                        <img
                          src={sortIconUpHover}
                          className="img-sort-up-hover"
                          alt="sort-up-hover"
                        />
                        <img
                          src={sortIconDownHover}
                          className="img-sort-down-hover"
                          alt="sort-down-hover"
                        />
                      </span>
                    </li>
                  ))}
                </ul>
              </div>
              <Divider orientation="left" plain></Divider>
              <div className="list-wrapper"></div>
            </div>
          </div>
          <div className="record-list-show">
            {tableSource.map((item) => (
              <div
                className="record-card-wrapper"
                title={`${item.company}----${item.office}`}
                key={item.id}
              >
                <div className="record-card-title">
                  <div className="card-company-title">
                    <span className="card-icon">
                      <img src={item.link} alt=""/>
                    </span>
                    {item.company}
                  </div>
                  <div className="card-office-title">{item.office}</div>
                </div>
                <br />
                <div className="record-card-content">
                  <ul className="record-card-baseinfo">
                    <li>
                      投递日期：
                      <Tag color="orange">{item.date}</Tag>
                    </li>
                    <li>
                      当前状态：
                      <Tag color="#d3e0ff">
                        {item.status === 0
                          ? deliveryIcon
                          : item.status === 1
                          ? filterIcon
                          : item.status === 2
                          ? writtenIcon
                          : item.status === 3
                          ? interviewIcon
                          : offerIcon}
                        <span style={{ color: "#316ef9" }}>
                          {options.map((op) => {
                            if (op.value === item.status) return op.label;
                          })}
                        </span>
                      </Tag>
                    </li>
                    <li>
                      笔试时间：{" "}
                      {!item.written_date ? (
                        "--"
                      ) : item.written ? (
                        <Tag color="red">{item.written_date}</Tag>
                      ) : (
                        <Tag color="green">{item.written_date}</Tag>
                      )}
                    </li>
                  </ul>
                  {item.first_interview_done !== ""&& (
                    <Divider orientation="left">面试情况</Divider>
                  )}
                  {item.first_interview_done !== "" && (
                    <div className="interview-process">
                      <ul className="interview-process-wrapper">
                        <li>
                          一面时间：{" "}
                          {
                            <Tag
                              color={
                                item.first_interview_done === 0
                                  ? "warning"
                                  : item.first_interview_done === 1
                                  ? "success"
                                  : "error"
                              }
                            >
                              {item.first_date}
                            </Tag>
                          }
                          {item.first_interview_done === 1 && (
                            <CheckCircleOutlined style={{ color: "green" }} />
                          )}
                          {item.first_interview_done === -1 && (
                            <CloseCircleOutlined style={{ color: "red" }} />
                          )}
                          {item.first_desc !== "" &&<span style={{marginLeft: '24px'}}>备注：{item.first_desc}</span>}
                        </li>
                        {item.second_interview_done !== "" && (
                          <li>
                            二面时间：{" "}
                            {
                              <Tag
                                color={
                                  item.second_interview_done === 0
                                    ? "warning"
                                    : item.second_interview_done === 1
                                    ? "success"
                                    : "error"
                                }
                              >
                                {item.second_date}
                              </Tag>
                            }
                            {item.second_interview_done === 1 && (
                              <CheckCircleOutlined style={{ color: "green" }} />
                            )}
                            {item.second_interview_done === -1 && (
                              <CloseCircleOutlined style={{ color: "red" }} />
                            )}
                            {item.second_desc !== "" &&<span style={{marginLeft: '24px'}}>备注：{item.second_desc}</span>}
                          </li>
                        )}
                        {item.third_interview_done &&item.third_interview_done !== "" && (
                          <li>
                            三面时间：{" "}
                            {
                              <Tag
                                color={
                                  item.third_interview_done === 0
                                    ? "warning"
                                    : item.third_interview_done === 1
                                    ? "success"
                                    : "error"
                                }
                              >
                                {item.third_date}
                              </Tag>
                            }
                            {item.third_interview_done === 1 && (
                              <CheckCircleOutlined style={{ color: "green" }} />
                            )}
                            {item.third_interview_done === -1 && (
                              <CloseCircleOutlined style={{ color: "red" }} />
                            )}
                            {item.third_desc !== "" &&<span style={{marginLeft: '24px'}}>备注：{item.third_desc}</span>}
                          </li>

                        )}
                        {item.hr_interview_done !== "" && (
                          <li>
                            hr面时间：{" "}
                            {
                              <Tag
                                color={
                                  item.hr_interview_done === 0
                                    ? "warning"
                                    : item.hr_interview_done === 1
                                    ? "success"
                                    : "error"
                                }
                              >
                                {item.hr_date}
                              </Tag>
                            }
                            {item.hr_interview_done === 1 && (
                              <CheckCircleOutlined style={{ color: "green" }} />
                            )}
                            {item.hr_interview_done === -1 && (
                              <CloseCircleOutlined style={{ color: "red" }} />
                            )}
                            {item.hr_desc !== "" &&<span style={{marginLeft: '24px'}}>备注：{item.hr_desc}</span>}
                          </li>

                        )}
                        
                      </ul>
                    </div>
                  )}
                  {/* 编辑按钮 */}
                  <div className="record-edit"
                  onClick={(e) => this.editRecord(e, item)}>
                    <span className="operation-icon" >编辑</span>
                  <FormOutlined style={{fontSize: '32px', color: "#3e6ef9", verticalAlign: 'middle'}}/>
                  </div>
                  {/* 删除按钮 */}
                  <div className="record-delete"
                  onClick={(e) => this.deleteRecord(e, item.id)}>
                    <span className="operation-icon">删除</span>
                  <DeleteOutlined style={{fontSize: '32px', color: "red", verticalAlign: 'middle'}}/>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
        {/* 编辑框 */}
        {isDiaglog && <EditRecord record={this.record} />}
      </div>
    );
  }
}

export default RecordList;
