import React from "react";
import { Modal, Form, Input } from "antd";

export default class EditRecord extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isopen: true,
    };
  }
  editRecordRef = React.createRef();

  handleCancel = () => {
    this.setState({
      ...this.state,
      isopen: false,
    });
  };
  handleOk = () => {
    this.setState({
      ...this.state,
      isopen: false,
    });
  };
  render() {
    const { record } = this.props;
    return (
      <div className="edit-container">
        <Modal
          title={record.company}
          open={this.state.isopen}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
          <Form ref={this.editRecordRef} name="editForm" preserve={false}>
            <Form.Item name="office" label="岗位名称">
              <Input placeholder="请输入岗位名称" defaultValue={record.office} />
            </Form.Item>
            <Form.Item name="city" label="工作地点">
              <Input placeholder="请输入工作地点" defaultValue={record.city} />
            </Form.Item>
            <Form.Item name="written_date" label="笔试时间">
              <Input placeholder="请输入笔试时间" defaultValue={record.written_date} />
            </Form.Item>
            <Form.Item name="first_date" label="一面时间">
              <Input placeholder="请输入一面时间" defaultValue={record.first_date} />
            </Form.Item>
          </Form>
        </Modal>
      </div>
    );
  }
}
